export default "#main-container{\n    width: 95%;\n    padding: 8px;\n  margin-left: 2.5%;\n}\n#wrapper{\n  background-color: rgba(0,0,0,0.8);\n  padding: 30px;\n}\n.title-container{\n  width: 100%;\n  font-size: 36px;\n  font-weight: 500;\n  padding: 0px 8px;\n  margin-bottom: 8px;\n  /* background-color: tomato; */\n}\n#about-container{\n  display: none;\n}\n.description{\n  width: 100%;\n  padding-right: 20px;\n  margin-right: 8px;\n  display: inline-block;\n  text-align: left;\n  /* background-color: yellowgreen; */\n  font-size: 14px;\n}\n.description-styling{\n  text-align: left;\n  padding: 0px 8px;\n}\n.picture{\n  background-color: white;\n  vertical-align: top;\n  text-align: left;\n  margin-right: 8px;\n  width: 100%;\n  -o-object-fit: contain;\n     object-fit: contain;\n  display: inline-block;\n}\nimg{\n  width: 100%;\n}\n#services-container{\n  width: 100%;\n  text-align: center;\n}\n/* Mobile Optimization */\n@media screen and (min-width: 641px){\n  #about-container-mobile{\n    display: none;\n  }\n  #about-container{\n    display: block;\n    width: 100%;\n    text-align: center;\n    margin-bottom: 8px;\n  }\n  /* .description{\n    width: calc(100% - 256px);\n  }\n  .picture{\n    width: 240px;\n    height: 320px;\n  } */\n  .description{\n    width: calc(65% - 8px);\n  }\n  .picture{\n    width: calc(35% - 8px);\n    -o-object-fit: cover;\n       object-fit: cover;\n  }\n\n}\n@media screen and (min-width: 1152px){\n  .description{\n    font-size: 18px;\n  }\n}\n/******* Service Component CSS *******/\n.services-wrapper{\n  padding: 20px;\n}\n.service-main-container{\n  /* height: 128px; */\n  padding: 8px;\n  margin-bottom: 16px;\n  /* background-color: violet; */\n  display: inline-block;\n  vertical-align: top;\n  text-align: center;\n}\n.logo-container{\n  width: 48px;\n  height: 100%;\n  display: inline-block;\n  vertical-align: top;\n  text-align: left;\n  /* background-color: teal; */\n}\n.service-details-container{\n  width: calc(100% - 48px);\n  height: 100%;\n  display: inline-block;\n  vertical-align: top;\n  text-align: justify;\n}\ni{\n  font-size: 28px;\n  margin: 4px 0px;\n  color: #9cbc68;\n}\n.service-title-container{\n  height: 30%;\n  width: 100%;\n  margin-bottom: 16px;\n  /* background-color: palegreen; */\n}\n.service-title{\n  font-size: 24px;\n  font-weight: bold;\n  margin: 0px;\n}\n.service-description-container{\n  text-align: left;\n  width: 90%;\n  height: 70%;\n  font-size: 16px;\n\n  /* background-color: peru; */\n}\n/* Mobile optimization */\n@media screen and (min-width: 320px){\n  .service-main-container{\n      width: 100%;\n  }\n}\n@media screen and (min-width: 640px){\n  .service-main-container{\n      width: 50%;\n  }\n}\n@media screen and (min-width: 1024px){\n  .service-main-container{\n      width: 33.33%;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"